* {
  margin: 0;
  padding: 0;
}
body {
  width: 90%;
  height: 90%;
  font-size: 14px;
  line-height: 1.6em;
  margin: auto;
}
.content {
  background-color: #1c1c67;
  color: white;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: xx-small;
  height: 95vh;
}
.nav1 {
  float: left;
  width: 50%;
  line-height: 0;
}
.nav1 h2 {
  font-size: 1.2rem;
  padding-bottom: 0;
  padding-inline-start: 20px;
  padding-top: 25px;
}
.nav1 h6 {
  font-weight: normal;
  font-size: 0.6rem;
  padding-inline-start: 22px;
}
.nav2 {
  float: right;
  padding-top: 25px;
  padding-inline-end: 20px;
}
ul {
  list-style: none;
}
li {
  display: inline-block;
  margin-top: 10px;
  margin-inline-end: 0;
}
.container1 h3 {
  text-align: center;
  font-weight: 500;
  font-size: 30px;
  color: hsl(51, 100%, 70%);
  font-family: italic;
  letter-spacing: 0.2rem;
}
.goodness {
  margin-inline-start: 37%;
  margin-top: 20px;
}
#finish {
  float: right;
  margin-inline-end: 20px;
  font-size: xx-large;
  font-weight: 500;
  padding: 8px;
  margin-top: 20px;
  border-radius: 0.4rem;
  border-style: none;
}
.main1 h1 {
  margin-left: 30%;
}
.main2 h1 {
  margin-left: 30%;
}
.nav2 li {
  margin-left: 10px;
}
.nav1 h1:hover {
  color: aquamarine;
}
.btnWork {
  background-color: #4caf50;
  border: none;
  color: white;
  padding: 10px 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 4px;
  cursor: pointer;
  width: 150px;
  border-radius: 3px;
}
.btnWorkS {
  background-color: #5de7b9;
  border: none;
  color: white;
  padding: 10px 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 4px;
  cursor: pointer;
  width: 150px;
  border-radius: 3px;
}
